import React from 'react';

const Navbar = () => {
  return (
    <nav className="sticky top-0 flex h-16 z-30 shadow bg-white">
      <div className="flex items-center justify-between w-full px-5">
        <div className="flex justify-between w-full md:hidden">
          <a className="flex items-center" href="https://www.codingal.com/">
            <img
              className="cursor-pointer mr-20"
              alt="Codingal"
              src="./logo-with-text.svg"
              style={{ width: '120px', height: '32px' }}
            />
          </a>
          <button
            className="md:hidden focus:outline-none"
            aria-label="Mobile Nav Bar"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
              className="text-gray-600"
            >
              <line x1="3" y1="12" x2="21" y2="12" />
              <line x1="3" y1="6" x2="21" y2="6" />
              <line x1="3" y1="18" x2="21" y2="18" />
            </svg>
          </button>
        </div>

        <div className="hidden md:flex md:flex-1 md:space-x-5">
          <a className="flex items-center" href="https://www.codingal.com/">
            <img
              className="cursor-pointer"
              alt="Codingal"
              src="./logo-with-text.svg"
              style={{ width: '120px', height: '32px' }}
            />
          </a>
          <NavLink href="https://www.codingal.com/quizzes/" text="Quizzes" />
          <NavLink href="https://www.codingal.com/competitions/" text="Competitions" />

          <div className="relative flex items-center">
            <button type="button" className="flex items-center group focus:outline-none">
              <span className="text-gray-600 group-hover:text-orange-500 font-medium text-lg">Courses</span>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
                className="ml-1 text-gray-600 group-hover:text-orange-500"
              >
                <polyline points="6 9 12 15 18 9" />
              </svg>
            </button>
            <div className="absolute hidden mt-2 bg-white rounded-lg shadow-lg group-hover:block">
              {courses.map(course => (
                <CourseLink key={course.name} {...course} />
              ))}
            </div>
          </div>
        </div>

        <div className="ml-4 flex items-center space-x-4">
          <button className="py-2 text-lg font-medium text-gray-600 hover:text-orange-500 focus:outline-none">Login</button>
          <button className="border rounded font-semibold px-6 py-2 text-orange-600 hover:bg-orange-100 focus:outline-none">Join class</button>
          <button className="border rounded font-semibold px-6 py-2 bg-orange-600 text-white hover:bg-orange-500 focus:outline-none">Try a free class</button>
        </div>
      </div>
    </nav>
  );
};

const NavLink = ({ href, text }) => (
  <a className="text-lg font-medium text-gray-600 hover:text-orange-500" href={href}>
    {text}
  </a>
);

const CourseLink = ({ href, name }) => (
  <div>
    <a
      href={href}
      target="_blank"
      rel="noopener noreferrer"
      className="flex items-center space-x-5 border-b border-gray-300 py-2 hover:bg-gray-100"
    >
      <span className="text-lg text-gray-600">{name}</span>
    </a>
  </div>
);

const courses = [
  { href: "https://www.codingal.com/courses/scratch-programming/", name: "Scratch Programming" },
  { href: "https://www.codingal.com/courses/python-for-kids/", name: "Python for Kids" },
  { href: "https://www.codingal.com/courses/game-development/", name: "Game Development for Kids" },
  { href: "https://www.codingal.com/courses/web-development/", name: "Website Development" },
  { href: "https://www.codingal.com/courses/app-development/", name: "App Development for Kids" },
  { href: "https://www.codingal.com/courses/data-science/", name: "Data Science for Teens" },
  { href: "https://www.codingal.com/courses/roblox-coding/", name: "Roblox" },
  { href: "https://www.codingal.com/courses/ap-computer-science-a/", name: "AP Computer Science A" },
  { href: "https://www.codingal.com/courses/ioi-algorithms-course/", name: "IOI Algorithms Course" },
];

export default Navbar;
